.Tilt{
  transition: all .2s linear;
  color: black;
  width: 100%;
  height: 100%;
  margin: auto;
  border-radius: 15px;
  background-image: linear-gradient(135deg, #ffffff 0%, #f2f2f2 100%);
  padding: 2vw 2vh 0vh 2vw;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  transform-style: preserve-3d;
  text-align: center;
  z-index: 2;
  /* overflow: hidden; */
  /* overflow-clip-margin: 0vw; */
}
.noTilt{
  /* border: 3px solid yellow; */
  transition: all .01s linear;
  color: black;
  width: 100%;
  height: 100%;
  margin: auto;
  border-radius: 15px;
  background-image: linear-gradient(135deg, #ffffff 0%, #f2f2f2 100%);
  padding: 2vw 2vh 0vh 2vw;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  /* transform-style: preserve-3d; */
  text-align: center;
  z-index: 2;
  /* overflow: hidden; */
  /* overflow-clip-margin: 0vw; */
}

@keyframes cardEntrance {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.cont:nth-child(1) {
  animation-delay: calc(1 * var(--stagger-delay));
}
.cont:nth-child(2) {
  animation-delay: calc(2 * var(--stagger-delay));
}
.cont:nth-child(3) {
  animation-delay: calc(3 * var(--stagger-delay));
}
.cont:nth-child(4) {
  animation-delay: calc(4 * var(--stagger-delay));
}
.cont:nth-child(5) {
  animation-delay: calc(5 * var(--stagger-delay));
}
.cont:nth-child(6) {
  animation-delay: calc(6 * var(--stagger-delay));
}
.cont:nth-child(7) {
  animation-delay: calc(7 * var(--stagger-delay));
}
.cont:nth-child(8) {
  animation-delay: calc(8 * var(--stagger-delay));
}
.cont:nth-child(9) {
  animation-delay: calc(9 * var(--stagger-delay));
}
.cont:nth-child(10) {
  animation-delay: calc(10 * var(--stagger-delay));
}
.cont:nth-child(11) {
  animation-delay: calc(11 * var(--stagger-delay));
}
.cont:nth-child(12) {
  animation-delay: calc(12 * var(--stagger-delay));
}


.Tilt:hover{
  z-index: 2;
  box-shadow: rgba(50, 50, 56, 0.2) 0px 10px 29px 20px;
  /* transform-style: preserve-3d; */
}

.Tilt-inner{
  transition: all .1s;
  transform: translateZ(50px);
  transform-style: preserve-3d;
  font-size: 1rem;
  /* height: 30%; */
  font-size:2.8vh;
  line-height: 90%;
  width: 95%;;
  text-align: center;
  margin: 0px auto;
  font-weight: 500;
  box-shadow: inherit;
}

.pics {
  transition: all .1s;
  margin: 0px auto;
  width: 70%;
  height: 60%;
  margin: 0px auto;
  margin-top: 1vh;
  border-radius: 35px;
  box-shadow: rgba(68, 68, 76, 0.2) 0px 7px 29px 0px;
}
.picsMobile {
  transition: all .1s;
  margin: 0px auto;
  width: 20%;
  height: 20%;
  margin: 0px auto;
  margin-top: 1vh;
  border-radius: 35px;
  box-shadow: rgba(68, 68, 76, 0.2) 0px 7px 29px 0px;
}

@media screen and (max-width: 600px) {
  .cont{
    min-width: 80vw;
  }
}


.button {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  padding: 0;
  border: 0;
  background: none;
  box-shadow: none;
  transform-style: preserve-3d;
  z-index: 1;
  color: inherit;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.sum {
  font-size: 1.3rem;
  margin: 0px 0px;
  overflow: hidden;
}
.sumMobile {
  font-size: 1.0rem;
  margin: 0px 0px;
  overflow: hidden;
}

.link {
  font-size: 30px;
}
.linkMobile {
  font-size: 20px;
}

.cont {
  display: flex;
  /* border: 2px solid black; */
  /* transition: all .1s linear; */
  color: black;
  color: inherit;
  box-shadow: inherit;
  width: 43vw;
  height: 35vh;
  /* margin: auto; */
  border-radius: 15px;
  /* padding: 2vw 2vh; */
  z-index: 100;
  animation-name: cardEntrance;
  animation-duration: 500ms;
  animation-timing-function: ease-out;
  animation-fill-mode: backwards;
  --stagger-delay: 50ms;
  animation-play-state: running;
  padding-bottom: 3vh;
}