.App {
  min-height: 100vh;
  background-image: linear-gradient(135deg, #ffffff 0%, #dbdcde 100%);
  /* border: 1px solid red; */
}

.search {
  /* border: 1px solid red; */
  min-width: 20vw;
  max-width: 25vw;
  margin: 0px auto;
  margin-bottom: 8vh;
  transform:translateY(5vh);
}
.search-bar {
  height: 2vh;
  border-radius: 15px;
  width:80%;
}
.search-button {
  top: 50%;
  transform: translateY(-50%);
  position:absolute;
  height: 5vh;
  width: 10vw;
  /* border-radius: 15px; */
  visibility: hidden;
  /* border: 1px solid red; */
}

.search-icon {
  padding: 1px;
  /* margin: 0px auto; */
  width: 2vh;
  height: 2vh;
  border: 1.5px solid rgb(0, 0, 0);
  color: black;
  display: flex;
  align-items: center;
  visibility: visible;
  border-radius:50%;
  -moz-border-radius:50%;
  -webkit-border-radius:50%;
  transition: all .1s ease;
}
.search-icon:hover {
  width: 2.5vh;
  height: 2.5vh;
  transition: all .1s ease;
  cursor: pointer;
}


.artCont {
  /* border: 1px solid blue; */
  display: flex; 
  flex-wrap: wrap;
  flex-direction: row; 
  /* margin: 0px auto ; */
  /* width: 100vw; */
  gap: 2vw;
  justify-content: center;
}

.Title {
  font-size: 60px;
  font-family: sans-serif;
  font-weight: 400;
  letter-spacing: 0.5vw;
  text-align: center;
  margin: 0vh auto 0vh auto;
  transform: translateY(3vh);
}